"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Link from "next/link";
import DeleteMenuButton from "./DeleteMenuButton";
import EditMenuForm from "./EditMenuForm";
import { Menu } from "@prisma/client";

interface Props {
  menu: Menu & {
    categories: {
      items: { id: string }[];
    }[];
  };
  locale: string;
}

export default function MenuCardClient({ menu, locale }: Props) {
  const [isEditing, setIsEditing] = useState(false);

  const itemsCount = menu.categories.reduce(
    (acc, cat) => acc + cat.items.length,
    0
  );

  return (
    <div className="relative rounded-2xl border border-white/10 bg-white/5 p-6">
      {/* VIEW MODE */}
      <div className={isEditing ? "opacity-40 pointer-events-none" : ""}>
        <Link href={`/${locale}/dashboard/menu/${menu.id}`}>
          <h2 className="text-lg font-medium mb-1">
            {menu.title ?? "Untitled menu"}
          </h2>
        </Link>

        {menu.description && (
          <p className="text-sm text-white/60 mb-4">{menu.description}</p>
        )}

        <div className="flex justify-between text-xs text-white/50">
          <span>{menu.categories.length} categories</span>
          <span>{itemsCount} items</span>
        </div>
      </div>

      {/* ACTIONS */}
      <div className="absolute top-3 right-3 flex gap-3">
        <button
          onClick={() => setIsEditing((v) => !v)}
          className="text-white/50 hover:text-white"
          title="Edit"
        >
          ✏️
        </button>

        <DeleteMenuButton menuId={menu.id} locale={locale} />
      </div>

      {/* EDIT DROPDOWN */}
      <AnimatePresence>
        {isEditing && (
          <motion.div
            initial={{ height: 0, opacity: 0 }}
            animate={{ height: "auto", opacity: 1 }}
            exit={{ height: 0, opacity: 0 }}
            transition={{ duration: 0.3, ease: "easeInOut" }}
            className="overflow-hidden mt-6"
          >
            <EditMenuForm menu={menu} onClose={() => setIsEditing(false)} />
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
